body {
  margin: 0;
  padding: 0;
  font-family: sans-serif; }

.container {
  width: 80%;
  margin: 0 auto;
  max-width: 1140px; }

.btn-purple {
  background: #9370d8;
  padding: 10px 40px;
  color: white;
  border-radius: 20px;
  border: none;
  font-size: 20px; }
  .btn-purple:hover {
    cursor: pointer;
    background: #7a55c4; }

.title {
  font-size: 80px;
  color: red; }

.customMegadraft {
  padding-bottom: 200px;
  text-align: center; }

.customMegadraft h2 {
  font-size: 40px;
  padding-bottom: 40px; }

/*  remove all default sizes for element boxes */
.block__content {
  background-color: white; }

.block__data, .dropdown__wrapper {
  display: none; }

.megadraft-editor .block__wrapper {
  border: none;
  box-shadow: none; }

.megadraft-editor .block__content {
  background: white; }

.megadraft-editor .block__content:before, .megadraft-editor .block__content:after {
  border: none; }

/* mobile styles */
@media (max-width: 600px) {
  .megadraft-editor .sidebar {
    z-index: 10;
    left: 30px; } }

nav {
  background: white;
  padding: 20px 0;
  position: fixed;
  width: 100%;
  z-index: 100; }

.navbar__content {
  display: flex;
  justify-content: space-between; }

footer {
  background: #121212;
  color: #f7f7f7;
  text-align: center;
  padding: 40px 0; }

.heroBanner {
  background: #c9f1ff;
  color: #2c2c2c;
  min-height: 80vh;
  text-align: center;
  padding-top: 20vh;
  position: relative;
  margin: 0 auto;
  overflow: hidden; }

.heroBanner h1 {
  font-size: 80px; }

.heroBanner h2 {
  font-size: 40px; }

.heroBanner button {
  background: mediumpurple;
  padding: 10px 40px;
  color: white;
  border-radius: 20px;
  border: none;
  font-size: 20px; }

.product {
  padding: 20px 10px;
  justify-self: center;
  text-align: center; }

.product img {
  max-width: 100%; }

@media (min-width: 600px) {
  .product {
    padding: 20px 80px; } }

.productGrid {
  padding: 100px 0; }

.productGrid .product__wrapper {
  width: 100%;
  display: inline-block; }

.productGrid__wrapper {
  text-align: center; }

.productGrid__wrapper h1 {
  font-size: 40px;
  padding: 100px 0 0 0; }

@media (min-width: 600px) {
  .productGrid .product__wrapper {
    width: 50%; } }

.titleBlock span {
  text-align: center;
  font-size: 60px;
  color: #121212;
  line-height: 3.7rem; }

.titleBlock {
  text-align: center; }
  .titleBlock .title .megadraft-editor .public-DraftEditorPlaceholder-root {
    text-align: center;
    font-size: 60px;
    position: inherit; }

.titleBlock .DraftEditor-root
.public-DraftEditorPlaceholder-root {
  position: inherit; }
  .titleBlock .DraftEditor-root
.public-DraftEditorPlaceholder-root .public-DraftEditorPlaceholder-inner {
    text-align: center;
    font-size: 60px;
    position: unset; }

.ReactModalPortal > div {
  opacity: 0; }

.ReactModalPortal .ReactModal__Overlay {
  align-items: center;
  display: flex;
  justify-content: center;
  transition: opacity 200ms ease-in-out; }

.ReactModalPortal .ReactModal__Overlay--after-open {
  opacity: 1; }

.ReactModalPortal .ReactModal__Overlay--before-close {
  opacity: 0; }

.modal {
  color: #121212;
  background: white;
  z-index: 2000;
  max-width: 80%;
  min-width: 10rem;
  outline: none;
  padding: 30px;
  text-align: center;
  position: absolute; }
  .modal h3 {
    display: inline; }
  .modal span {
    position: absolute;
    top: 14px;
    right: 20px; }
    .modal span i, .modal span svg {
      color: lightgrey;
      cursor: pointer; }



